* {
  font-family: "Poppins", sans-serif;
  letter-spacing: 2px;
}

html {
  scroll-behavior: smooth;
}

:root {
  --bgColor1: #1f1f1f;
  --bgColor2: #383838;
  --theme: #0aaab3;
  --bg-color: rgb(20, 20, 20);
  --card-color: rgb(23, 23, 23);
}

body {
  background-color: var(--bgColor1);
  color: #fff;
}

/* CSS for navbar */

.navBar {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  justify-content: space-between;
  align-items: center;
  backdrop-filter: blur(4px);
  width: 100%;
  height: 60px;
  font-weight: 600;
  z-index: 999;
  padding-top: 25px;
}

.navBar p {
  font-size: 1.1rem;
  cursor: default;
  height: 100%;
  margin-left: 50px;
  color: var(--theme);
}

.navBar ul {
  height: 100%;
  position: relative;
  top: 0;
  right: 50px;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 50px;
}

.navBar li {
  user-select: none;
  height: 100%;
  position: relative;
  list-style: none;
  overflow: hidden;
  top: 0;
  bottom: 0;
  color: #fff;
  cursor: pointer;
}

.navBar li:hover {
  color: var(--theme);
}

.navBar li::before {
  position: absolute;
  left: 50%;
  top: 30px;
  content: "";
  width: 0;
  height: 4px;
  background-color: var(--theme);
  transition: left 0.3s ease-in-out, width 0.3s ease-in-out,
    height 0.3s ease-in-out, background-color 0.3s linear;
}

.navBar li:hover::before {
  left: 0;
  width: 100%;
  height: 4px;
  transition: left 0.3s ease-in-out, width 0.3s ease-in-out,
    height 0.3s ease-in-out, background-color 0.3s linear;
}

/* CSS for profile page */
#profile {
  width: 100%;
  position: relative;
  top: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 100px;
  margin-top: 100px;
}

#profile > * {
  width: 35%;
}

.intro {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
}

#profile h1 {
  margin: 0;
  color: var(--theme);
  font-size: 4rem;
  cursor: default;
}

#profile h3 {
  margin: 0;
  cursor: default;
  font-size: 2rem;
}

.intro span {
  background-color: var(--theme);
  width: 30%;
  height: 4px;
  cursor: default;
  margin-top: 20px;
}

.btn {
  position: relative;
  width: 30%;
  text-transform: uppercase;
  background-color: #fff;
  font-size: 1.2rem;
  border: none;
  outline: none;
  border-radius: 30px;
  padding: 5px;
  padding-top: 8px;
  padding-bottom: 8px;
  font-weight: 600;
  color: var(--bgColor1);
  overflow: hidden;
  transition: color 0.3s linear;
  cursor: pointer;
  z-index: 1;
}

.btn:hover {
  background-color: var(--theme);
  transition: background-color 0s linear 300ms;
}

.btn::before {
  content: "";
  border-radius: 30px;
  position: absolute;
  background-color: var(--theme);
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transform: translateX(-100%);
  transition: transform 0.3s ease-in, background-color 0.3s linear;
  z-index: -1;
}

.btn:hover::before {
  transform: translateX(0);
  transition: transform 0.3s ease-out, background-color 0.3s linear;
}

.btn-small{
  width: 40%;
}

.btn-container{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.profile-img {
  aspect-ratio: 1/1;
  width: 25%;
  border-radius: 50%;
}

/* CSS for Section component */

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin-bottom: 150px;
  width: 100%;
}

.section h1 {
  cursor: default;
  border-bottom: 4px solid var(--theme);
}

.heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.cards {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 50px;
  width: calc(100% - 20px);
}

.cards:hover > .card::after {
  opacity: 1;
}

.card {
  background-color: rgba(255, 255, 255, 0.02);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 400px;
}

.card:hover::before {
  opacity: 1;
}

.card::before,
.card::after {
  border-radius: inherit;
  content: "";
  height: 100%;
  left: 0px;
  opacity: 0;
  position: absolute;
  top: 0px;
  transition: opacity 500ms;
  width: 100%;
}

.card::before {
  background: radial-gradient(
    800px circle at var(--mouse-x) var(--mouse-y),
    rgba(255, 255, 255, 0.01),
    transparent 40%
  );
  z-index: 3;
}

.card::after {
  background: radial-gradient(
    600px circle at var(--mouse-x) var(--mouse-y),
    rgba(255, 255, 255, 0.05),
    transparent 40%
  );
  z-index: 1;
}

.card > .card-content {
  margin-left: 20px;
  background-color: transparent;
  border-radius: inherit;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  inset: 1px;
  padding: 10px;
  position: absolute;
  z-index: 4;
  height: 100%;
}

.card > .card-content > ul{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.card > .card-content .btn{
  position: relative;
  width: 20%;
  font-size: 0.8rem;
  margin-top: auto;
  margin-bottom: 30px;
  margin-left: 20px;
  z-index: 1;
}

.card > .card-content .btn::before {
  z-index: -1;
}

#certificates .card{
  cursor: pointer;
  transform: scale(1);
  transition: transform 500ms cubic-bezier(0.1, 0.7, 1.0, 1.01);
}

#certificates .card:hover{
  transform: scale(1.1);
  transition: transform 500ms cubic-bezier(0.1, 0.7, 1.0, 1.01);
}

#certificates .card::before {
  background: radial-gradient(
    800px circle at var(--mouse-x) var(--mouse-y),
    rgba(255, 255, 255, 0.01),
    transparent 40%
  );
  z-index: 3;
}

#certificates .card::after {
  background: radial-gradient(
    600px circle at var(--mouse-x) var(--mouse-y),
    #0aabb313,
    transparent 40%
  );
  z-index: 1;
}

.contact-details{
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact-details a{
  text-decoration: none;
  position: relative;
  top: -5px;
  color: #fff;
  border-bottom: 2px solid #fff;
}

/* CSS for showing popup view for images */

.popup{
  position: fixed;
  top: 30%;
  aspect-ratio: 6/4;
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  z-index: -1;
  border-radius: 10px;
  transition: top 0.4s ease-out, opacity 0.4s ease-out;
}

.popup img{
  width: 100%;
  border-radius: 10px;
}

.skeleton-popup{
  width: 100%;
  border-radius: 10px;
  height: 100%;
}

.popup span{
  align-self: flex-end;
  cursor: pointer;
}

.popup.active{
  z-index: 9999;
  top: 20%;
  opacity: 1;
  transition: top 0.4s ease-out, opacity 0.4s ease-out;
}

/* CSS for blur effect */

/* The overlay */
#overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .6);
  z-index: 9998;
  opacity: 0;
  visibility: hidden;
}

/* The blur effect */
#overlay.active {
   /* Adjust the blur radius below */
   -webkit-filter: blur(2px);
   -moz-filter: blur(2px);
   -o-filter: blur(2px);
   -ms-filter: blur(2px);
   filter: blur(2px);
   opacity: 1;
   visibility: visible;
}


/* Media query for devices with width less than 1350px */

@media only screen and (max-width : 1350px) and ( min-width : 950px){
  #profile > .intro {
    width: 50%;
  }
}

@media only screen and (max-width : 950px) and (min-width : 865px){
  #profile{
    flex-direction: column-reverse;
    justify-content: flex-end;
    align-items: center;
    gap: 30px;
  }
  #profile > .intro{
    width: 70%;
  }
}

@media only screen and (max-width : 865px){

  .navBar {
    flex-direction: column;
    justify-content: flex-start;
    height: 150px;
    gap: 10px;
  }
  
  .navBar p {
    margin: 0;
    height: 25%;
    font-size: 1rem;
  }
  
  .navBar ul {
    height: 50%;
    right: 20px;
    bottom: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    gap: 20px;
  }
  
  .navBar li {
    height: 50%;
    position: relative;
    list-style: none;
    overflow: hidden;
  }
  
  .navBar span {
    height: 100%;
    position: relative;
    top: 0;
    bottom: 0;
    color: #fff;
    cursor: pointer;
  }
  
  .navBar span:hover {
    color: var(--theme);
  }
  
  .navBar span::before {
    position: absolute;
    left: 50%;
    top: 30px;
    content: "";
    width: 0;
    height: 4px;
    background-color: var(--theme);
    transition: left 0.3s ease-in-out, width 0.3s ease-in-out,
      height 0.3s ease-in-out, background-color 0.3s linear;
  }
  
  .navBar span:hover::before {
    left: 0;
    width: 100%;
    height: 4px;
    transition: left 0.3s ease-in-out, width 0.3s ease-in-out,
      height 0.3s ease-in-out, background-color 0.3s linear;
  }

  #profile{
    flex-direction: column-reverse;
    justify-content: flex-end;
    align-items: center;
    gap: 30px;
    margin-top: 200px;
  }
  #profile > .intro{
    width: 70%;
  }

  #profile > .intro > h1{
    font-size: 2rem;
  }

  #profile > .intro > h3{
    font-size: 1.3rem;
  }

  #profile > .intro > .btn{
    position: relative;
    width: 45%;
    font-size: 1rem;
    border-radius: 10px;
    padding: 3px;
    padding-top: 8px;
    padding-bottom: 8px;
    font-weight: 600;
  }

  .btn-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-between;
  }

  #profile > .intro > .btn-small{
    width: 45%;
    font-size: 1rem;
  }

  .popup{
    aspect-ratio: 6/4;
    width: 70%;
  }
}

@media only screen and (max-width : 500px){
  .btn{
    width: 60%;
  }
}